<template>
  <!-- 头部公共内容 -->
  <div class="page-head">
    <div class="top">
      <span class="title">{{title.left}}</span>
      <!-- <img class="arrow" src="static/img/arrow.png" alt=""> -->
      <div class="menu-content">
        <span class="menu-item active" v-if="title.right">{{title.right}}</span>
        <!-- <a href="http://www.baidu.com" class="menu-item active">知识讲解</a>
        <a href="http://www.baidu.com" class="menu-item">优质课堂</a>
        <a href="http://www.baidu.com" class="menu-item">室内游戏</a>
        <a href="http://www.baidu.com" class="menu-item">体能训练</a> -->
      </div>
    </div>
    <!-- 左上角装饰 -->
    <img src="static/img/title-top-left.png" class="decorate-left">
    <img src="static/img/top-right.png" class="decorate-right">
    <img src="static/img/right-bottom.png" class="decorate-bottom">
  </div>
</template>

<style lang="less">
  @import url('../../../static/css/common.less');
  .page-head{
    overflow: hidden;
    width: 100%;
    height: 100px;
    border-bottom: 2px solid #309195;
    padding: 0 54px 0 180px ;
    font-size: 0;
    .top{
      .title{
        font-size:36px;
        font-weight: bold;
        line-height: 100px;
        float: left;
      }
      .arrow{
        width: 40px;
        height: 28px;
        margin-left: 26px;
      }
      .menu-content{
        float: left;
        .menu-item{
          height: 34px;
          box-sizing: border-box;
          line-height: 100px;
          font-size: 24px;
          padding: 0 32px;
          color: #7f7f7f;
          height: 40px;
          text-decoration: none;
          cursor: pointer;
          &:not(:first-child){
            border-left: 1px solid #7f7f7f;
          }
          &.router-link-active{
            color: #c00000;
            font-weight: bold;
          }
          &.active{
            color: #c00000;
            font-weight: bold;
          }
        }
      }
    }
    .decorate-left{
      position: fixed;
      z-index: 1000;
      top: 0;
      left: 0;
      width: 110px;
      height: 120px;
    }
    .decorate-right{
      position: fixed;
      z-index: 1000;
      top: 46px;
      right: 0;
      width: 31px;
      height: 50px;
    }
    .decorate-bottom{
      position: fixed;
      z-index: 1000;
      bottom: 0;
      right: 0;
      width: 74px;
      height: 81px;
    }
  }
</style>
<script>
export default {
  props:['title'],
  data(){
    return{
    }
  }
}
</script>



