<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftpic-page-body">
    <transition name="toRight">
      <div class="right hover-effect" v-show="dataArr.txtObj[0].showNow">
        <img :src="dataArr.bigImg">
        <div class="arrow-bg title-fs">
          {{dataArr.imgTitle}}
        </div>
        <div class="arrow title-fs">
          {{dataArr.imgTitle}}
        </div>
      </div>
    </transition>
    <div class="left">
      <div class="txt-box hover-effect" v-for="(item, index) in dataArr.txtObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="txt-title normal-txt-fs">{{item.title}}</div>
            <div class="txt-item normal-txt-fs" v-for="(txtItem, index) in item.content"><i><b>{{index + 1}}&nbsp;&nbsp;</b></i>{{txtItem}}</div>
          </div>
        </transition>
      </div>
    </div>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 7% 0px 7%;
    box-sizing: border-box;
    .left{
      width: 40%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .txt-box{
        .txt-title{
          // width: 20%;
          height: 10%;
          line-height: 200%;
          text-align: center;
          color: #fff;
          margin-bottom: 4%;
        }
        .txt-item{
          line-height: 130%;
          margin-top: 4%;
        }
        &:nth-child(2n){
          .txt-title{
            background: #a0bf0d;
          }
        }
        &:nth-child(2n + 1){
          .txt-title{
            background: #319095;
          }
        }
      }
    }
    .right{
      background: #dedede;
      width: 50%;
      height:80%;
      background-color: #d8d0c0;
      margin-right: 10%;
      float: left;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
      .arrow{
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        background:  #f5841c;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
      .arrow-bg{
        left: 4%;
        background:  #c46209;
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'文明如厕讲卫生',
          right:'室内游戏＞游戏内容',
        },
        dataArr:{
          imgTitle:'超级连接', 
          bigImg:'static/img/wenmingruce1.jpeg',
          txtObj:[
            {
              title:'游戏规则',
              content:[
                '学生分组，各组成纵队站好',
                '各组队员依次张开双腿，相邻两人的前后脚相抵，全组队员连成一条线',
                '坚持5~15s为成功',
                '最长的队伍获胜',
                // '双人：选择左右学生为游戏伙伴',
                // '团队：分组，以另一组学生作为游戏竞争伙伴，通过3轮或5轮游戏来决定个人胜负，最终胜负人数之和来决定团队的胜负',
                // '两人面对面，距离约为0.5米',
                // '两人面对面，距离约为0.5米',
              ],
              showNow:false
            },
           
          ],
        }
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.txtObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
    }
  }
</script>


