<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftxtmultpic-page-body" @click="orderEvent">
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-fs" v-for="(itemTxt, index) in item.content">
                <p>{{itemTxt}}</p>
              </div>
            </div>
          </div>
        </transition>
      </div>
   </div>
   <transition name="toLeft">
    <div class="right" v-show="dataArr.leftObj[0].showNow">
        <img class="hover-effect" v-for="(imdSrc, index) in dataArr.bigImg" :src="imdSrc">
    </div>
   </transition>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftxtmultpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 10% 0px 10%;
    box-sizing: border-box;
    .left{
      width: 55%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .item{
        .item-left{
          float: left;
          width: 78px;
          height: 78px;
          img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            border: solid 1px #319095;
          }
        }
        .item-right{
          float: left;
          width: calc(~'100% - 110px');
          margin-left: 32px;
        }
      }
    }
    .right{
      width: 40%;
      height:100%;
      margin-left: 5%;
      margin-top: -5%;
      float: left;
      img{
        float: left;
        width: 50%;
        height: 100%/3;
        cursor: pointer;
        clip-path: inset(2% round 20% 0% 20% 0%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'知识讲解＞教学内容',
        },
        dataArr:{
          leftObj:[
            {
              title:'教学目标',
              content:[
                '1.认知目标：了解营养不良与肥胖。',
                '2.技能目标：了解造成肥胖和营养不良的原因及危害。',
                '3.情感目标：培养良好的锻炼意识，鼓励养成良好的生活习惯。',
              ],
              titleImg:'static/img/qiu4.png',
              showNow:false
            },
            {
              title:'教学重点',
              content:[
                '营养不良与肥胖的原因及危害',
              ],
              titleImg:'static/img/qiu4.png',
              showNow:false
            },
            {
              title:'教学难点',
              content:[
                '如何预防营养不良与肥胖',
              ],
              titleImg:'static/img/qiu4.png',
              showNow:false
            },
          ],
          //右边的几个图片，不能超过六个
          bigImg:[
            'static/img/yybl1.jpg',
            'static/img/fp1.jpg',
            'static/img/yybl4.png',
            'static/img/fp2.jpg',
            'static/img/yybl2.jpg',
            'static/img/fp3.jpg',
            ]
        }
      }
    },
    methods:{
      orderEvent: function(){
        this.orderDisplay(this.txtArr);
      }
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.leftObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
    }
  }
</script>


