<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftpic-page-body">
    <transition name="toRight">
      <div class="right hover-effect" v-show="dataArr.txtObj[0].showNow">
        <img :src="dataArr.bigImg">
        <div class="arrow-bg title-fs">
          {{dataArr.imgTitle}}
        </div>
        <div class="arrow title-fs">
          {{dataArr.imgTitle}}
        </div>
      </div>
    </transition>
    <div class="left">
      <div class="txt-box hover-effect" v-for="(item, index) in dataArr.txtObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="txt-title normal-txt-fs">{{item.title}}</div>
            <div class="txt-item normal-txt-fs" v-for="(txtItem, index) in item.content">{{txtItem}}</div>
          </div>
        </transition>
      </div>
    </div>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 7% 0px 7%;
    box-sizing: border-box;
    .left{
      width: 40%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .txt-box{
        margin-bottom: 2%;
        .txt-title{
          // min-width: 20%;
          height: 10%;
          line-height: 200%;
          text-align: center;
          color: #fff;
          margin-bottom: 4%;
        }
        .txt-item{
          line-height: 130%;
          margin-top: 4%;
        }
        &:nth-child(2n){
          .txt-title{
            background: #a0bf0d;
          }
        }
        &:nth-child(2n + 1){
          .txt-title{
            background: #319095;
          }
        }
      }
    }
    .right{
      background: #dedede;
      width: 50%;
      height:80%;
      background-color: #d8d0c0;
      margin-right: 10%;
      float: left;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
      .arrow{
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        background:  #f5841c;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
      .arrow-bg{
        left: 4%;
        background:  #c46209;
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'每天坚持一小时体育锻炼',
          right:'知识讲解＞教学内容',
        },
        dataArr:{
          imgTitle:'每天坚持一小时体育锻炼', 
          bigImg:'static/img/mtjc6.jpg',
          txtObj:[
            {
              title:'',
              content:[
                '（5）教师提出“学习忙没有时间进行体育锻炼”的误区',
                '（6）学生自学“化整为零，合理安排锻炼时间”内容，并结合学校、社区、村社的体育设施条件，对各个时段的锻炼方式进行丰富，提出更多适合自己的、新颖的锻炼方式',
                '（7）学生分组交流，共同设计利用学习的空余时间进行身体锻炼的方案 ',
                // '（3） 同桌推出代表与教师和同学交流分享',
                // '【小结】正确地认识体育锻炼，能够坚定体育锻炼的信念，增强动力；学习化整为零、合理安排锻炼时间的锻炼策略，能够为我们每天坚持一小时体育锻炼提供方法上的借鉴。那么怎样是每天的体育锻炼更有效呢？需要对每天的体育锻炼进行合理计划，也就是设定目标，制定计划',
              ],
              showNow:false
            },
            {
          
              showNow:false
            },
          ],
        }
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.txtObj;
      obj[0].showNow = true;
      if(obj[1]){
        setTimeout(function(){
            obj[1].showNow = true;
        },200)
      }
    }
  }
</script>


