<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftpic-page-body">
    <transition name="toRight">
      <div class="right hover-effect" v-show="dataArr.txtObj[0].showNow">
        <img :src="dataArr.bigImg">
        <div class="arrow-bg title-fs">
          {{dataArr.imgTitle}}
        </div>
        <div class="arrow title-fs">
          {{dataArr.imgTitle}}
        </div>
      </div>
    </transition>
    <div class="left">
      <div class="txt-box hover-effect" v-for="(item, index) in dataArr.txtObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="txt-title normal-txt-fs">{{item.title}}</div>
            <div class="txt-item normal-txt-fs" v-for="(txtItem, index) in item.content"><i><b>{{index + 1}}&nbsp;&nbsp;</b></i>{{txtItem}}</div>
          </div>
        </transition>
      </div>
    </div>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/leftpic.css";
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'安全运动促健康',
          right:'知识讲解＞教学内容',
        },
        dataArr:{
          imgTitle:'体育运动好处多', 
          bigImg:'static/img/aqyd4.jpg',
          txtObj:[
            {
              title:'',
              content:[
                '身体方面：增强体质、消除疲劳，提高工作学习效率，提高抗病能力，增强身体适应自然的能力',
                '心理方面：调节人体紧张情绪，改善身心健康，恢复体力精力；陶冶情操，保持健康心态，提高自信心',
                '社会适应：培养团结、协作的集体主义精神',
     
              ],
              showNow:false
            },
            {
              // title:'动作要领',
              // content:[
              //   '第一条内容',
              //   '第二条内容',
              // ],
              showNow:false
            },
          ],
        }
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.txtObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
    }
  }
</script>


