<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="videotop-page-body">
   <transition name="toTop">
     <div class="height_100p" v-show="show">
        <div class="right hover-effect">
          <iframe class="yk" :src="videoSrc" frameborder=0 allowfullscreen></iframe>
        </div>
        <div class="txt-content normal-txt-fs">{{description}}</div>
     </div>
   </transition>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/videoTop.css";
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'坐、立、行我最美',
          right:'知识讲解＞教学内容',
        },
        videoSrc:'',//此项不用填写自动生成
        description:'解放军叔叔无论寒冬酷暑，每天都站立两个小时以上，举枪敬礼持续30分钟不动，走正步不少于5公里，大家也要坚持正确的坐立行姿势啊！',
        vid:'XMTMyNzcwNzMxMg',
        show:false
      }
    },
    methods:{
    },
    mounted(){
      this.videoSrc = 'http://player.youku.com/embed/' + this.vid + '==?client_id=78b2cfcefe58c217';
      this.show = true;
    }
  }
</script>


