<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
 <transition name="toTop">
  <div class="pic-page-body" v-show="show">
    <img class="img-item hover-effect click-effect" v-for="(imgItem, index) in imgArr" :src="imgItem">
  </div>
 </transition>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/pic.css";
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'',
        },
        //最多十二张图片
        imgArr:[
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
          'static/img/test.jpg',
        ],
        show:false
      }
    },
    mounted(){
      this.show = true;
    }
  }
</script>


