<template>
 <!-- 大标题页 就是tmd上下一张大图，中间一个数字＋内容 -->
  <div class="index-page-body" @click="showPPT">
    <!-- 上背景 -->
    <transition name="toBottom"> 
      <div class="top-bg" v-show="show">
        <img src="static/img/home-top-left.png">
      </div>
    </transition>
    <!-- 下背景 -->
    <transition name="toTop"> 
      <div class="bottom-bg" v-show="show">
        <img src="static/img/home-right-bottom.png">
      </div>
    </transition>
    <transition name="transparentAppear"> 
      <div class="height_100p" v-show="show">
        <!-- 页面顺序，其实就是md12345 -->
        <div class="title-index">
          <span class="num">{{pageIndex}}</span>
          <span class="english">{{pageIndexEN}}</span>
        </div>
        <!-- 中间文字 -->
        <div class="txt-box">
          <img class="left-pic" src="static/img/txt.png" alt="" srcset="">
          <span class="txt">{{pageName}}</span>
          <img class="right-pic" src="static/img/txt1.png" alt="" srcset="">
        </div>
      </div>
    </transition>
  </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .index-page-body{
    width: 100%;
    height: 100%;
    position: relative;
    .top-bg{
      position: absolute;
      width: 100%;
      height: 64%;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .bottom-bg{
      position: absolute;
      width: 42%;
      height: 30%;
      bottom: 0;
      right: 0;
      img{
        width: 100%;
        height: 100%;
      }
    }
    .title-index{
      position: absolute;
      top:22%;
      left: 42%;
      width: 40%;
      height: 15.6%;
      .num{
        font-family: Calibri;
        font-weight: bold;
        font-size: 170px;
        color: #826c4a;
      }
      .english{
        font-family: Calibri;
        font-size: 60px;
        color: #d9d9d9;
      }
    }
    .txt-box{
      position: absolute;
      width:100%;
      height: 35%;
      margin-top: 22%;
      // background: #0000002c;
      display: flex;
      justify-content: center;
      align-items: center;
      .left-pic{
        margin-top: -100px;
        margin-right: 20px;
      }
      .right-pic{
        margin-top: 100px;
      }
      .txt{
        letter-spacing: 20px;
        font-size:6em;
        font-weight: bold;
        color: #826c4a;
      }
      
    }
  }
</style>
<script>
  export default{
    components: {
    },
    data(){
      return{
        //控制动画
        'show':false,
        //页面文本信息
        'pageIndex':' 3',
        'pageIndexEN':'part three',
        'pageName':'体能训练'
      }
    },
    methods:{
      showPPT:function(){
        this.show = true;
      }
    },
    mounted(){
      $('.page-head').css({'display':'none'});
      this.show = true;
    },
    destroyed(){
      $('.page-head').css({'display':'block'});
    }
  }
</script>


