<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="txtandpic-page-body">

    <div class="item-box hover-effect click-effect" v-for="(obj, index) in contenArr">
      <transition name="toTop">
        <div v-show="obj.showNow">
          <div class="img-content">
            <img :src="obj.imgSrc" alt="" srcset="">
          </div>
          <div class="title title-fs">
            {{obj.title}}
          </div>
          <div class="content normal-txt-fs">
            {{obj.content}}
          </div>
        </div>
      </transition>
    </div>
    
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/txtAndPic.css";
  
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'',
        },
        contenArr:[
          {
            'showNow':false,
            'imgSrc':'static/img/title-top-left.png',
            'title':'足球外观1',
            'content':'比赛用球应为圆形，它的外壳应用皮革或其它许可的材料制成，在它的结构中不得使用可能伤害运动员的材料',
          },
          {
            'showNow':false,
            'imgSrc':'static/img/title-top-left.png',
            'title':'足球外观2',
            'content':'比赛用球应为圆形，它的外壳应用皮革或其它许可的材料制成，在它的结构中不得使用可能伤害运动员的材料',
          },
          {
            'showNow':false,
            'imgSrc':'static/img/title-top-left.png',
            'title':'足球外观3',
            'content':'比赛用球应为圆形，它的外壳应用皮革或其它许可的材料制成，在它的结构中不得使用可能伤害运动员的材料',
          },
          {
            'showNow':false,
            'imgSrc':'static/img/title-top-left.png',
            'title':'足球外观4',
            'content':'比赛用球应为圆形，它的外壳应用皮革或其它许可的材料制成，在它的结构中不得使用可能伤害运动员的材料',
          },
        ],
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.contenArr;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
      setTimeout(function(){
        obj[3].showNow = true;
      },400)
    }
  }
</script>


