<template>  
<div class="height_100p">      
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="lefttext-page-body" @click="orderEvent">
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left" v-show="item.titleImg">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-fs">{{item.content}}</div>
            </div>
          </div>
        </transition>
      </div>
   </div>
   <transition name="toLeft">
    <div class="right hover-effect" v-show="dataArr.leftObj[1].showNow">
        <img :src="dataArr.bigImg">
    </div>
   </transition>
  </div>
  </div>
</template>

<style lang="less">
@import "../../../static/css/common.less";
@import "../../../static/css/animate.css";
.lefttext-page-body {
  width: 100%;
  height: calc(~"100% - 100px");
  padding: 9% 10% 0px 15%;
  box-sizing: border-box;
  .left {
    width: 45%;
    height: 80%;
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .item {
      .item-left {
        float: left;
        width: 78px;
        height: 78px;
        img {
          width: 100%;
          height: 100%;
          border-radius: 50%;
          border: solid 1px #319095;
        }
      }
      .item-right {
        float: left;
        width: calc(~"100% - 110px");
        margin-left: 32px;
      }
    }
  }
  .right {
    background: #dedede;
    width: 50%;
    height: 70%;
    background-color: #d8d0c0;
    margin-left: 5%;
    float: left;
    img {
      width: 100%;
      height: 100%;
      cursor: pointer;
    }
  }
}
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'健康饮水益处多',
          right:'知识讲解＞教学内容',
        },
      dataArr: {
        leftObj: [
          {
            title: "我们要喝多少水",
            content:
              "每天排出10杯水，从食物中获取4杯水，我们要喝6杯水",
            titleImg: "static/img/qiu4.png",
            showNow: false
          },
          {
            title: "怎样喝水才健康",
            content:
              "不要等到口渴才喝水，不要过快补水，吃饭的时候不宜喝水，饮料不宜多喝",
            titleImg: "static/img/qiu4.png",
            showNow: false
          },
          {
            title: "",
            content:
              "",
            titleImg: "",
            showNow: false
          },
          {
            title: "",
            content:
              "",
            titleImg: "",
            showNow: false
          },
        ],
        bigImg: "static/img/jkys2.jpg"
      }
    };
  },
  methods: {
    orderEvent: function() {
      this.orderDisplay(this.txtArr);
    }
  },
  mounted() {
    //各个item依次出现，我真tm流弊，能写出这么神奇的东西
    var obj = this.dataArr.leftObj;
    obj[0].showNow = true;
    setTimeout(function() {
      obj[1].showNow = true;
    }, 200);
    setTimeout(function() {
      obj[2].showNow = true;
    }, 300);
  }
};
</script>


