<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="tpsecond-page-body">
    <div class="item-box hover-effect click-effect" v-for="(obj, index) in contenArr">
      <transition name="toTop">
        <div v-show="obj.showNow" class="height_100p">
          <div class="img-content">
            <img :src="obj.imgSrc" alt="" srcset="">
          </div>
          <div class="txt-box">
            <div class="title title-fs">
              {{obj.title}}
            </div>
            <div class="content normal-txt-fs" v-for="(txt, index) in obj.content">
              {{txt}}
            </div>
          </div>
        </div>
      </transition>
    </div>

  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .tpsecond-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 100px 100px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    .item-box{
      width: 23%;
      height: 90%;
      text-align: center;
      .img-content{
        width: 130px;
        height: 130px;
        margin: 0 auto;
        transform: rotate(45deg);
        // border-radius: 50%;
        overflow: hidden;
        border: 1px solid #dedede;
        background: #fff;
        // &:nth-child(1){
          //   background-color: #5fcacb;
        // }
        // &:nth-child(2){
          //   background-color: #fdb900;
        // }
        // &:nth-child(3){
          //   background-color: #a1c00d;
        // }
        // &:nth-child(4){
          //   background-color: #f5841c;
        // }
        // &:nth-child(5){
          //   background-color: #5fcacb;
        // }
        // &:nth-child(6){
          //   background-color: #fdb900;
        // }
        // &:nth-child(7){
          //   background-color: #a1c00d;
        // }
        // &:nth-child(8){
          //   background-color: #f5841c;
        // }
        img{
          width: 100%;
          height: 100%;
        }
      }
      .txt-box{
        height: 100%;
        margin-top: -65px;
        border: solid 2px #d9d9d9;
        padding: 100px 5% 0;
        background-color: #f2f2f2;
        .title{
          margin: 10% 0 5%;
        }
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'生活方式与健康',
          right:'室内游戏＞游戏内容',
        },
        contenArr:[
          {
            'showNow':false,
            'imgSrc':'static/img/title-top-left.png',
            'title':'游戏内容',
            'content':[
              '手臂力量角逐',
              // '争分夺秒',
            ],
          },
          {
            'showNow':false,
            'imgSrc':'static/img/title-top-left.png',
            'title':'游戏目的',
            'content':[
              '通过不同的手臂力量练习、提高学生的上肢力量，培养学生坚强的意志品质'
            ],
          },
        
        ],
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.contenArr;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
      setTimeout(function(){
        obj[3].showNow = true;
      },400)
    }
  }
</script>


