<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="text-page-body" @click="orderEvent">
    <div class="color-block" v-for="(txtItem,index) in txtArr">
      <transition name="toTop">
        <div class="hover-effect click-effect height_100p" v-show="txtItem.txtObj.show">
          <div class="arrow" v-if="index%2 != 0">
            <img :src="'static/img/arrow'+(index+1)+'.png'" alt="" srcset="">
          </div>
          <div class="txt-content">
            <div class="title title-fs">{{txtItem.txtObj.title}}</div>
            <div class="txt normal-txt-fs">{{txtItem.txtObj.content}}</div>
          </div>
          <div class="arrow" v-if="index%2 == 0">
            <img :src="'static/img/arrow'+(index+1)+'.png'" alt="" srcset="">
          </div>
        </div>
      </transition>
    </div>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/text.css";
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'',
        },
        txtArr:
        [
          {
            txtObj: {
              'show':true,
              'showOrder':'1',
              'title':'添加标题',
              'content':'内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容'
            }
          },
          {
            txtObj: {
              'show':true,
              'showOrder':'2',
              'title':'添加标题',
              'content':'内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容'
            }
          },
          {
            txtObj: {
              'show':true,
              'showOrder':'3',
              'title':'添加标题',
              'content':'内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容'
            }
          },
          {
            txtObj: {
              'show':true,
              'showOrder':'4',
              'title':'添加标题',
              'content':'内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容'
            }
          }
        ]
      }
    },
    methods:{
      orderEvent: function(){
        this.orderDisplay(this.txtArr);
      }
    }
  }
</script>


