<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftvideo-page-body">
   <transition name="toRight">
    <div class="right hover-effect" v-show="dataArr.leftObj[0].showNow">
        <iframe class="yk" :src="videoSrc" frameborder=0 allowfullscreen></iframe>
    </div>
   </transition>
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-f">{{item.content}}</div>
            </div>
          </div>
        </transition>
      </div>
   </div>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/leftVideo.css";
  
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'',
        },
        dataArr:{
          leftObj:[
            {
              title:'标题',
              content:'内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分',
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            {
              title:'标题',
              content:'内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分',
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            {
              title:'标题',
              content:'内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分内容分',
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
          ],
        },
        videoSrc:'',//此项不用填写自动生成
        description:'视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述视频描述述视频描述',
        vid:'XMzQzNDQ0MzcxNg'
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.leftObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
      this.videoSrc = 'http://player.youku.com/embed/' + this.vid + '==?client_id=78b2cfcefe58c217';
    }
  }
</script>


