<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftpic-page-body">
    <transition name="toRight">
      <div class="right hover-effect" v-show="dataArr.txtObj[0].showNow">
        <img :src="dataArr.bigImg">
        <div class="arrow-bg title-fs">
          {{dataArr.imgTitle}}
        </div>
        <div class="arrow title-fs">
          {{dataArr.imgTitle}}
        </div>
      </div>
    </transition>
    <div class="left">
      <div class="txt-box hover-effect" v-for="(item, index) in dataArr.txtObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="txt-title normal-txt-fs">{{item.title}}</div>
            <div class="txt-item normal-txt-fs" v-for="(txtItem, index) in item.content">{{txtItem}}</div>
          </div>
        </transition>
      </div>
    </div>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 7% 0px 7%;
    box-sizing: border-box;
    .left{
      width: 40%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .txt-box{
        margin-bottom: 2%;
        .txt-title{
          // min-width: 20%;
          height: 10%;
          line-height: 200%;
          text-align: center;
          color: #fff;
          margin-bottom: 4%;
        }
        .txt-item{
          line-height: 130%;
          margin-top: 4%;
        }
        &:nth-child(2n){
          .txt-title{
            background: #a0bf0d;
          }
        }
        &:nth-child(2n + 1){
          .txt-title{
            background: #319095;
          }
        }
      }
    }
    .right{
      background: #dedede;
      width: 50%;
      height:80%;
      background-color: #d8d0c0;
      margin-right: 10%;
      float: left;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
      .arrow{
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        background:  #f5841c;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
      .arrow-bg{
        left: 4%;
        background:  #c46209;
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'每天坚持一小时体育锻炼',
          right:'知识讲解＞教学内容',
        },
        dataArr:{
          imgTitle:'周体育锻炼计划', 
          bigImg:'static/img/mtjc1.png',
          txtObj:[
            {
              title:'',
              content:[
                '（1）学生阅读左侧“周体育锻炼计划”表，教师引导学生观察说出构成锻炼计划表的主要内容有哪些',
                '（2）教师归纳总结制定“周体育锻炼计划”主要内容',
                '（3）教师依次讲解怎样制定目标、如何确定锻炼时间、怎样选择锻炼内容、如何确定运动负荷、怎样对计划实施情况进行评价等内容',
                '（4）提示制定“周体育锻炼计划”注意事项，解答学生疑问',
                '（5）布置作业：学生课后根据个人体能状态制定简单的周体育锻炼计划',
              ],
              showNow:false
            },
            {
          
              showNow:false
            },
          ],
        }
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.txtObj;
      obj[0].showNow = true;
      if(obj[1]){
        setTimeout(function(){
            obj[1].showNow = true;
        },200)
      }
    }
  }
</script>


