<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="video-page-body">
   <transition name="toTop">
     <div class="height_100p" v-show="show">
       <h1 class="title title-fs">{{videoTitle}}</h1><br>
        <div class="right hover-effect">
          <iframe class="yk" :src="videoSrc" frameborder=0 allowfullscreen></iframe>
        </div>
     </div>
   </transition>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .video-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 2% 7% 0px 7%;
    box-sizing: border-box;
    .title{
      text-align: center;
    }
    .right{
      background: #dedede;
      width: 77%;
      height:80%;
      float: left;
      margin-left: 11.5%;
      .yk{
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
    }
  }
</style>
<script type="text/javascript">
    
</script>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'生活方式与健康',
          right:'室内游戏＞热身运动',
        },
        videoSrc:'',//此项不用填写自动生成
        videoTitle:'',
        vid:'XMzEyMTUyOTY2MA',
        show:false,
      }
    },
    methods:{

    },
    mounted(){
      this.videoSrc = 'http://player.youku.com/embed/' + this.vid + '==?client_id=78b2cfcefe58c217';
      this.show = true;
    }
  }
</script>


