<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="textone-page-body">
    <transition name="toLeft">
      <div class="music-play" v-show="show" title="单击【播放】、【暂停】音乐">
        <img src="static/img/music.png" @click="playMusic" :style="{transform:rotateAngle,transition:time}">
      </div>
    </transition>
    <audio id="bg-music" :src="txtObj.bgMusic"></audio>
    <transition name="toTop">
      <div v-show="show">
        <div class="t-c title-fs">
          {{txtObj.title}}
        </div>
        <div class="text-content t-c normal-txt-fs"><br>
          <div class="item" v-for="(item, index) in txtObj.content">{{item}}</div>
        </div>
      </div>
    </transition>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .textone-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 153px 100px 180px;
    .music-play{
      position: absolute;
      cursor: pointer;
      top:20%;
      right: 5%;
      width: 78px;
      height: 78px;
      border-radius: 50%;
      background-color: #f5841c;
      box-shadow: 0px 0px 12px 0px 
        rgba(245, 132, 28, 0.22);
      border: solid 1px #f1821c;
      text-align: center;
      img{
        margin-top: 20%;
        width: 40px;
        height: 40px;
      }
    }
    .text-content{
      .item{
        line-height: 2.5em;
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  import $ from 'jquery'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'生活方式与健康',
          right:'知识讲解＞课堂引入',
        },
        rotateAngle:'rotate(0deg)',//播放按钮旋转角度
        time: '20000000000000s',
        musicActive:false,
        show:false,
        txtObj:{
          title:'',
          content:[
            // '猜歌名，并说说歌词中都唱了什么？',
            '播放歌曲',
            '学生猜歌名',
            '学生举手回答歌词中都唱了什么',
            // '接下来，我们一起来探究水的重要作用',
            // '小老鼠，吱吱叫，看到猫儿就跑掉。牛伯伯，好强壮，看到红布向前闯。',
            // '小老鼠，吱吱叫，看到猫儿就跑掉。牛伯伯，好强壮，看到红布向前闯。',
          ],
          bgMusic:'static/sound/jiankangge.mp3'
        }
      }
    },
    methods:{
      playMusic:function(){
        var music = $('#bg-music')[0];
        if(this.musicActive){
          this.rotateAngle = 'rotate(0deg)'
          this.musicActive = false;
          this.time = '0s';
          music.pause();
        }else{
          music.play();
          this.time = '20000000000000s';
          this.rotateAngle = 'rotate(1000000000000000deg)';
          this.musicActive = true;
        }
      }
    },
    mounted(){
      this.show = true;
    }
    
  }
</script>


