<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="txtapf-page-body">
    <div class="item-box hover-effect" v-for="(item, index) in dataArr">
      <transition name="toTop">
        <div class="height_100p" v-show="show">
          <div class="left-txt">
            <div class="title title-fs">
              {{item.title}}
            </div>
            <div class="normal-txt-fs">{{item.content}}
            </div>
          </div>
          <div class="right-pic">
            <img :src="item.imgSrc">
          </div>
        </div>
      </transition>
    </div>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/txtAndPicFourth.css";
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'',
        },
        dataArr:[
          {
            title:'标题',
            content:'内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊',
            imgSrc:'static/img/test.jpg',
          },
          {
            title:'标题',
            content:'内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊内容啊',
            imgSrc:'static/img/test.jpg',
          },
          {
            title:'标题',
            content:'内容啊内容啊内容啊内容啊',
            imgSrc:'static/img/test.jpg',
          },
          {
            title:'标题',
            content:'内容啊内容啊内容啊内容啊内容啊内容啊',
            imgSrc:'static/img/test.jpg',
          },
        ],
        show:false,
      }
    },
    methods:{
    },
    mounted(){
      this.show = true;
    }
  }
</script>


