<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="textone-page-body">
    <audio id="bg-music" :src="txtObj.bgMusic"></audio>
    <transition name="toTop">
      <div v-show="show">
        <div class="t-c title-fs">
          {{txtObj.title}}
        </div>
        <div class="text-content normal-txt-fs"><br>
          <div class="item" v-for="(item, index) in txtObj.content">{{item}}</div>
        </div>
      </div>
    </transition>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .textone-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 220px 100px;
    overflow-y: auto;
    .music-play{
      position: absolute;
      cursor: pointer;
      top:20%;
      right: 5%;
      width: 78px;
      height: 78px;
      border-radius: 50%;
      background-color: #f5841c;
      box-shadow: 0px 0px 12px 0px 
        rgba(245, 132, 28, 0.22);
      border: solid 1px #f1821c;
      text-align: center;
      img{
        margin-top: 20%;
        width: 40px;
        height: 40px;
      }
    }
    .text-content{
      .item{
        line-height: 2.5em;
        &:nth-child(2n){
          font-weight: bold;
        }
      }
    }
  }
</style>
<script>
  import $ from 'jquery'
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'知识讲解＞问卷调查',
        },
        rotateAngle:'rotate(0deg)',//播放按钮旋转角度
        time: '20000000000000s',
        musicActive:false,
        show:false,
        txtObj:{
          title:'问卷调查',
          content:[
            '1、 你知道营养素缺乏会引起哪些后果吗？',
            ' A．身体消瘦    B．经常生病    C．个子长得慢     D．学习效果差     E．以上都是',
            '2 、你认为营养好坏与学习成绩有关系吗？',
            'A．有关系        B．没有关系        C．不确定',
            '3、注重营养膳食还可使你长得更高一些，变得更聪明一些，你认为？',
            'A．说得正确      B．说得有一定道理     C．错        D．不知道',
            '4、你认为自己目前的饮食是否合理（营养是否全面）？',
            'A．很合理          B．比较合理          C．不合理     D.不知道',
            '5、你认为一日三餐中要吃得最多的餐次应该是？',
            'A．早餐    B．中餐      C．晚餐',
            '6、你是否每天吃早餐？',
            'A．每天吃   B．差不多每天吃  C．不能保证经常吃   D.基本不吃',
            '7、你早餐的合理组合更接近于以下那种？',
            'A．粮谷类     B．粮谷类加乳、豆制品    C．粮谷类加肉蛋类     D．肉蛋类加乳、豆制品  E.粮谷类、肉蛋类加乳、豆制品',
            '8、在一日三餐中，你吃得最好（指食物种类最多）的一餐通常是？',
            'A．早餐     B．中餐    C．晚餐      D．三餐都差不多    E．中餐和晚餐差不多好',
            '9、你认为多吃零食对健康的影响是？',
            'A．有益       B．无益也无害        C．有害         D．不知道',
            '10、以下零食哪些是你经常吃的？',
            'A．膨化食品       B．方便面      C．含糖饮料      D．饼干        E.其他（请具体说明）',
            '11、你经常吃新鲜蔬菜、水果吗？',
            'A．每天都吃多量    B．每天吃少量    C．不是每天吃    D．几乎不吃或很少吃 ',
            '12、你一周吃几次荤菜（包括蛋类）？',
            'A．每天吃   B．3～5次   C．1～3次  D.偶尔吃一次',
            '13、你经常吃豆浆、豆腐、豆腐干等豆制品吗？',
            'A．经常吃    B．偶尔吃    C．几乎不吃',
           
          ],
          bgMusic:'static/sound/bg.mp3'
        }
      }
    },
    methods:{
      playMusic:function(){
        var music = $('#bg-music')[0];
        if(this.musicActive){
          this.rotateAngle = 'rotate(0deg)'
          this.musicActive = false;
          this.time = '0s';
          music.pause();
        }else{
          music.play();
          this.time = '20000000000000s';
          this.rotateAngle = 'rotate(1000000000000000deg)';
          this.musicActive = true;
        }
      }
    },
    mounted(){
      this.show = true;
    }
    
  }
</script>


