<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftpic-page-body">
    <transition name="toRight">
      <div class="right hover-effect" v-show="dataArr.txtObj[0].showNow">
        <img :src="dataArr.bigImg">
        <div class="arrow-bg title-fs">
          {{dataArr.imgTitle}}
        </div>
        <div class="arrow title-fs">
          {{dataArr.imgTitle}}
        </div>
      </div>
    </transition>
    <div class="left">
      <div class="txt-box hover-effect" v-for="(item, index) in dataArr.txtObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="txt-title normal-txt-fs">{{item.title}}</div>
            <div class="txt-item normal-txt-fs" v-for="(txtItem, index) in item.content"><i><b>{{index + 1}}&nbsp;&nbsp;</b></i>{{txtItem}}</div>
          </div>
        </transition>
      </div>
    </div>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 7% 0px 7%;
    box-sizing: border-box;
    .left{
      width: 40%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .txt-box{
        .txt-title{
          // width: 20%;
          height: 10%;
          line-height: 200%;
          text-align: center;
          color: #fff;
          margin-bottom: 4%;
        }
        .txt-item{
          line-height: 130%;
          margin-top: 4%;
        }
        &:nth-child(2n){
          .txt-title{
            background: #a0bf0d;
          }
        }
        &:nth-child(2n + 1){
          .txt-title{
            background: #319095;
          }
        }
      }
    }
    .right{
      background: #dedede;
      width: 50%;
      height:80%;
      background-color: #d8d0c0;
      margin-right: 10%;
      float: left;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
      .arrow{
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        background:  #f5841c;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
      .arrow-bg{
        left: 4%;
        background:  #c46209;
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'安全地进行游戏',
          right:'室内游戏＞游戏内容',
        },
        dataArr:{
          imgTitle:'倒着说', 
          bigImg:'static/img/anquan66.jpg',
          txtObj:[
            {
              title:'游戏规则',
              content:[
                '所有学生围成一圈，老师规定字数，比如四个字',
                '第一个人说四个字，例如“我是好人”，第二个人倒着说“人好是我”',
                '第三个人接着说出一个四个字的句子，第四个人倒着说',
                '每个人有五秒反应时间，说的句子不能重复，大家顺时针说下去，五秒内没有说出来的、句子字数说错的和倒着说错的人为失败',
                '失败的人离开位置，其他人继续循环，最后剩下的一个人即为获胜',
              ],
              showNow:false
            },
           
          ],
        }
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.txtObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
    }
  }
</script>


