<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftxtmultpic-page-body new" @click="orderEvent">
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-fs" v-for="(itemTxt, index) in item.content">
                <p>{{itemTxt}}</p>
              </div>  
            </div>
          </div>
        </transition>
      </div>
   </div>
   <transition name="toLeft">
    <div class="right" v-show="dataArr.leftObj[0].showNow">
        <img class="hover-effect" v-for="(imdSrc, index) in dataArr.bigImg" :src="imdSrc">
    </div>
   </transition>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/leftTextMultPic.css";
  .leftxtmultpic-page-body.new{
    .left{
      justify-content:center;
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'',
        },
        dataArr:{
          leftObj:[
            {
              title:'标题',
              content:[
                '1、认知目标：了解什么是营养不良与肥胖。',
                '2、技能目标：了解造成肥胖和营养不良的原因及危害。',
                '3、情感目标：培养良好的锻炼意识，鼓励养成良好的生活习惯。',
              ],
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            {
              title:'标题',
              content:[
                '1、认知目标：了解什么是营养不良与肥胖。',
                '2、技能目标：了解造成肥胖和营养不良的原因及危害。',
                '3、情感目标：培养良好的锻炼意识，鼓励养成良好的生活习惯。',
              ],
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
            {
              title:'标题',
              content:[
                '1、认知目标：了解什么是营养不良与肥胖。',
                '2、技能目标：了解造成肥胖和营养不良的原因及危害。',
                '3、情感目标：培养良好的锻炼意识，鼓励养成良好的生活习惯。',
              ],
              titleImg:'static/img/videotest.jpg',
              showNow:false
            },
          ],
          //右边的几个图片，不能超过六个
          bigImg:[
            'static/img/videotest.jpg',
            'static/img/videotest.jpg',
            'static/img/videotest.jpg',
            'static/img/videotest.jpg',
            'static/img/videotest.jpg',
            'static/img/videotest.jpg',
            ]
        }
      }
    },
    methods:{
      orderEvent: function(){
        this.orderDisplay(this.txtArr);
      }
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.leftObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
    }
  }
</script>


