<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftxtmultpic-page-body" @click="orderEvent">
   <div class="left">
      <div class="item hover-effect click-effect" v-for="(item, index) in dataArr.leftObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="item-left">
              <img :src="item.titleImg">
            </div>
            <div class="item-right">
              <div class="title title-fs">{{item.title}}</div>
              <div class="content normal-txt-fs" v-for="(itemTxt, index) in item.content">
                <p>{{itemTxt}}</p>
              </div>
            </div>
          </div>
        </transition>
      </div>
   </div>
   <transition name="toLeft">
    <div class="right" v-show="dataArr.leftObj[0].showNow">
        <img class="hover-effect" v-for="(imdSrc, index) in dataArr.bigImg" :src="imdSrc">
    </div>
   </transition>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftxtmultpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 10% 0px 10%;
    box-sizing: border-box;
    .left{
      width: 55%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      .item{
        .item-left{
          float: left;
          width: 78px;
          height: 78px;
          img{
            width: 100%;
            height: 100%;
            border-radius: 50%;
            border: solid 1px #319095;
          }
        }
        .item-right{
          float: left;
          width: calc(~'100% - 110px');
          margin-left: 32px;
        }
      }
    }
    .right{
      width: 40%;
      height:100%;
      margin-left: 5%;
      margin-top: -5%;
      float: left;
      img{
        float: left;
        width: 50%;
        height: 100%/3;
        cursor: pointer;
        clip-path: inset(2% round 20% 0% 20% 0%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'安全运动促健康',
          right:'知识讲解＞教学内容',
        },
        dataArr:{
          leftObj:[
            {
              title:'学生回答',
              content:[
                // '请学生分析平时运动中会出现哪些安全隐患',
                '请学生举例在教学过程中看到或者听到的安全事故或者伤害事故，分析造成事故的原因',
 
              ],
              titleImg:'static/img/qiu4.png',
              showNow:false
            },
            {
              title:'教师总结',
              content:[
                // '我们应该怎样做？',
                '1.运动方式不合理，如相互追打',
                '2.不良运动习惯，如饭后就激烈运动',
                '3.运动场地原因，如下雨天打球踢球',
                '4.注意力不集中，如跑步看手机',
                // '5.远离有电等危险的地方',
               
              ],
              titleImg:'static/img/qiu4.png',
              showNow:false
            },
            // {
            //   title:'齐读儿歌《远离甲型流感》',
            //   content:[
            //     '甲型流感不可怕，发现症状早报告。',
            //     '勤洗手来爱清洁，开窗通风不能忘。',
            //     '人多别去凑热闹，运动跑步又做操。',
            //     '预防措施抓得牢，全家健康开口笑。',
            //   ],
            //   titleImg:'static/img/videotest.jpg',
            //   showNow:false
            // },
          ],
          //右边的几个图片，不能超过六个
          bigImg:[
            'static/img/aqyd555555.jpg',
            'static/img/aqyd555.png',
            'static/img/aqyd555.jpg',
            'static/img/aqyd5.jpg',
            'static/img/aqyd55.jpg',
            'static/img/aqyd5555.jpg',
            ]
        }
      }
    },
    methods:{
      orderEvent: function(){
        this.orderDisplay(this.txtArr);
      }
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.leftObj;
      obj[0].showNow = true;
      setTimeout(function(){
        obj[1].showNow = true;
      },200)
      setTimeout(function(){
        obj[2].showNow = true;
      },300)
    }
  }
</script>


