<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="texttwo-page-body">
    <transition name="toBottom">
      <div class="content one" v-show="show">
        <div class="liubianxing hover-effect">
          <p>{{blue.title1}}</p>
          <p>{{blue.title2}}</p>
        </div>
        <div class="txt normal-txt-fs">
          {{blue.content}}
        </div>
      </div>
    </transition>
    <transition name="toRight">
      <div class="content two" v-show="show">
        <div class="liubianxing hover-effect">
          <p>{{orange.title1}}</p>
          <p>{{orange.title2}}</p>
        </div>
        <div class="txt normal-txt-fs">
          {{orange.content}}
        </div>
      </div>
    </transition>
    <transition name="toLeft">
      <div class="content three" v-show="show">
        <div class="liubianxing hover-effect">
          <p>{{green.title1}}</p>
          <p>{{green.title2}}</p>
        </div>
        <div class="txt normal-txt-fs">
          {{green.content}}
        </div>
      </div>
    </transition>
  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/textTwo.css";
  
</style>
<script>
  import pptHead from './header.vue'
  import $ from 'jquery'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'',
        },
        show:false,
        // 蓝色六边形
        blue:{
          title1:'知识',
          title2:'目标',
          content:'内容内容内容内容内容内容内容内容'
        },
        // 橘色六边形
        orange:{
          title1:'技能',
          title2:'目标',
          content:'的风格化的风格化的风格化的风格的风格化的风格化的风格和的风格化的风格'
        },
        // 绿色六边形
        green:{
          title1:'态度',
          title2:'目标',
          content:'内容内容内容内容内容内容内容内容'
        },
      }
    },
    mounted(){
      this.show = true
    }
  }
</script>


