<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftpic-page-body">
    <transition name="toRight">
      <div class="right hover-effect" v-show="dataArr.txtObj[0].showNow">
        <img :src="dataArr.bigImg">
        <div class="arrow-bg title-fs">
          {{dataArr.imgTitle}}
        </div>
        <div class="arrow title-fs">
          {{dataArr.imgTitle}}
        </div>
      </div>
    </transition>
    <div class="left">
      <div class="txt-box hover-effect" v-for="(item, index) in dataArr.txtObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="txt-title normal-txt-fs">{{item.title}}</div>
            <div class="txt-item normal-txt-fs" v-for="(txtItem, index) in item.content">{{txtItem}}</div>
          </div>
        </transition>
      </div>
    </div>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 7% 0px 7%;
    box-sizing: border-box;
    .left{
      width: 40%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .txt-box{
        margin-bottom: 2%;
        .txt-title{
          // min-width: 20%;
          height: 10%;
          line-height: 200%;
          text-align: center;
          color: #fff;
          margin-bottom: 4%;
        }
        .txt-item{
          line-height: 130%;
          margin-top: 4%;
        }
        &:nth-child(2n){
          .txt-title{
            background: #a0bf0d;
          }
        }
        &:nth-child(2n + 1){
          .txt-title{
            background: #319095;
          }
        }
      }
    }
    .right{
      background: #dedede;
      width: 50%;
      height:80%;
      background-color: #d8d0c0;
      margin-right: 10%;
      float: left;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
      .arrow{
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        background:  #f5841c;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
      .arrow-bg{
        left: 4%;
        background:  #c46209;
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'常见传染病的预防',
          right:'知识讲解＞教学内容',
        },
        dataArr:{
          imgTitle:'学校常见传染病种类及症状', 
          bigImg:'static/img/cjcr44444.jpg',
          txtObj:[
            {
              title:'流行性腮腺炎',
              content:[
                // '简称流腮，由腮腺炎病毒所引起，全年均可发病，但以冬春为主',
                // '在学校及幼托机构易造成暴发流行，临床特征为发热及腮腺肿、痛，腮腺炎其病虽不可怕，然而其并发症却十分可怕，可能导致：',
                '①男生睾丸炎：较大儿童及体弱患儿易并发睾丸炎，常有一侧或双侧睾丸肿大、疼痛，若治疗不及时出现睾丸萎缩而引起无精症，故而不生育',
                '②卵巢炎：10岁以上女患儿易并发卵巢炎，症状是小腹部及腰骶部疼痛、全身乏力，发烧较重可达39℃以上，治疗不及时，婚后不孕',
                '③脑膜脑炎：在腮腺肿大一周后出现嗜睡、呕吐、头痛、颈项强直、发烧39℃以上，一般无抽搐',
                '隔离期限：至腮腺完全消肿后3天止，自发病起约3周时间',
              ],
              showNow:false
            },
            {
          
              showNow:false
            },
          ],
        }
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.txtObj;
      obj[0].showNow = true;
      if(obj[1]){
        setTimeout(function(){
            obj[1].showNow = true;
        },200)
      }
    }
  }
</script>


