<template> 
<div class="height_100p">   
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="tapt-page-body">
    <div class="item-box hover-effect" v-for="(item, index) in dataArr">
      <transition name="toLeft">
        <div class="height_100p" v-show="show">
          <div class="title-arrow normal-txt-fs">
            {{item.title}}
          </div>
          <div class="title-box normal-txt-fs">
            {{item.title}}
          </div>
          <div class="img">
            <img src="static/img/test.jpg" alt="" srcset="">
          </div>
          <div class="txt-content normal-txt-fs">
            <div>{{item.content}}</div>
          </div>
        </div>
      </transition>
    </div>


  </div>
</div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  @import "../../../static/css/txtAndPicThird.css";
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'营养不良与肥胖',
          right:'',
        },
        dataArr:[
          {
            title:'标题标标题',
            content:'内容内容内容内容',
            imgSrc:'static/img/test.jpg'
          },
          {
            title:'标题',
            content:'内容内容内容内容内容内容内容内容内容内容内容内容内容',
            imgSrc:'static/img/test.jpg'
          },
          {
            title:'标题',
            content:'内容内容内容内容内容内容内容内容内容内容',
            imgSrc:'static/img/test.jpg'
          },
          {
            title:'标题',
            content:'内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容内容',
            imgSrc:'static/img/test.jpg'
          },
        ],
        show:false,
      }
    },
    methods:{
    },
    mounted(){
      this.show = true;
    }
  }
</script>


