<template>
 <div class="height_100p">
  <pptHead :title="titleMsg"></pptHead>
 <!-- ppt中间内容 -->
  <div class="leftpic-page-body">
    <transition name="toRight">
      <div class="right hover-effect" v-show="dataArr.txtObj[0].showNow">
        <img :src="dataArr.bigImg">
        <div class="arrow-bg title-fs">
          {{dataArr.imgTitle}}
        </div>
        <div class="arrow title-fs">
          {{dataArr.imgTitle}}
        </div>
      </div>
    </transition>
    <div class="left">
      <div class="txt-box hover-effect" v-for="(item, index) in dataArr.txtObj">
        <transition name="toTop">
          <div v-show="item.showNow">
            <div class="txt-title normal-txt-fs">{{item.title}}</div>
            <div class="txt-item normal-txt-fs" v-for="(txtItem, index) in item.content">{{txtItem}}</div>
          </div>
        </transition>
      </div>
    </div>
  </div>
 </div>
</template>

<style lang="less">
  @import "../../../static/css/common.less";
  @import "../../../static/css/animate.css";
  .leftpic-page-body{
    width: 100%;
    height: calc(~'100% - 100px');
    padding: 6% 7% 0px 7%;
    box-sizing: border-box;
    .left{
      width: 40%;
      height: 80%;
      float: left;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      .txt-box{
        margin-bottom: 2%;
        .txt-title{
          // min-width: 20%;
          height: 10%;
          line-height: 200%;
          text-align: center;
          color: #fff;
          margin-bottom: 4%;
        }
        .txt-item{
          line-height: 130%;
          margin-top: 4%;
        }
        &:nth-child(2n){
          .txt-title{
            background: #a0bf0d;
          }
        }
        &:nth-child(2n + 1){
          .txt-title{
            background: #319095;
          }
        }
      }
    }
    .right{
      background: #dedede;
      width: 50%;
      height:80%;
      background-color: #d8d0c0;
      margin-right: 10%;
      float: left;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        cursor: pointer;
      }
      .arrow{
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        background:  #f5841c;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
      .arrow-bg{
        left: 4%;
        background:  #c46209;
        position: absolute;
        display: flex;
        align-items: center;
        top:-6%;
        height: 12%;
        min-width: 34%;
        padding: 0 8% 0 3%;
        font-size: 24px;
        font-weight: boild;
        color: #fff;
        clip-path:polygon(0 0, 90% 0, 100% 50%, 90% 100%, 0 100%);
      }
    }
  }
</style>
<script>
  import pptHead from './header.vue'
  export default{
    components: {
      'pptHead': pptHead
    },
    data(){
      return {
        //头部左边标题
        titleMsg:{
          left:'常见传染病的预防',
          right:'知识讲解＞教学内容',
        },
        dataArr:{
          imgTitle:'打败传染病', 
          bigImg:'static/img/cjcr33333.jpg',
          txtObj:[
            {
              title:'学生回答',
              content:[
                '传染病对人类这么恐怖，我们要怎样预防传染病呢？',
                '你们有预防传染病的经历吗？',
                '指导学生根据自己患感冒时的亲身经历等分析现象，学会提出问题',
              ],
              showNow:false
            },
            {
              // title:'自主探究',
              // content:[
              //   '同组(或同桌)相互合作，必要时求得老师的帮助',
              //   '实事求是地整理分析探究资料，得出初步结论(或看法)',
              //   '学生大胆陈述自己的观点，相互交流、争辩，激起思维共鸣',
              //   '师生互动交流成果，自主完善结论，进行知识整合',
              //   '及时引导学生总结出传染病预防措施',
              //   '突破重点、难点',
              //   '【小结】同学们，通过本节知识的学习，我们是不是要养成良好的生活习惯，树立健康的生活态度，关注健康呀？',
              // ],
              showNow:false
            },
          ],
        }
      }
    },
    methods:{
    },
    mounted(){
      //各个item依次出现，我真tm流弊，能写出这么神奇的东西
      var obj = this.dataArr.txtObj;
      obj[0].showNow = true;
      if(obj[1]){
        setTimeout(function(){
            obj[1].showNow = true;
        },200)
      }
    }
  }
</script>


